import React,{useState, useEffect} from 'react'
import axios from "axios";
import Cookies from "js-cookie";
import { connect } from "react-redux";
import {Link} from "react-router-dom"
import Header from './sections/Header';
import SideNav from './sections/SideNav';
import Content from './sections/Content';
import { saveUserDataAction } from '../store/actions/userActions';

const Dashboard = ({rxsetUser, app}) => {
    let token = Cookies.get('TOKEN');
   

    const getUserDetails = async ()=>{
    try{
        const userDetails = await axios.post('/users/me', {}, {
           headers: {
              "X-TOKEN" : token
           }
        })
        console.log(userDetails.data)
        if(userDetails.data.status){
            console.log(userDetails.data)
            rxsetUser(userDetails.data.user)
        }
     }catch(err){
        console.log("error", err)
        // getUserDetails()
     }
  
  }

  useEffect(() => {
       getUserDetails() 
   
 }, [])
    return (
        <div class="main-page-wrapper">
            <div class={`dropdown-overlay ${app.appModal?"active":""}`}></div>
            <div id="dashboard-wrapper">
                <Header/>
                <div class="container">
					<div id="dashboard-main-body" class="clearfix">
                        <SideNav/>
                        <Content/>
                    </div>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = state =>({
    app:state.app
})
const mapDispatchToProps = dispatch =>({
    rxsetUser: data => {
        dispatch(saveUserDataAction(data))
    }
})
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);