import {
    TOGGLE_APP_MODAL,
    TOGGLE_APP_MENU
    } from "../actions/types"
    
    const initialState = {
        appModal: false,
        appMenu: false,
    }
    
    
    export default (state = initialState, action) => {
        switch (action.type) {
           case TOGGLE_APP_MODAL: return {
               ...state,
               appModal: !state.appModal
           }
           case TOGGLE_APP_MENU: return {
               ...state,
               appMenu: !state.appMenu
           }

          default:
            return state;
        }
      };
      